import React from 'react'
import { Table, Column, Cell } from 'fixed-data-table-2'

export default function SequenceColumn(props) {
	const {
		enableLink, //
		startSequence, //
		enableAddDelrow, //
		footer, //
		onAddrow, //
		onDelrow, //
		onClick, //
	} = props

	const getContent = (ps) => {

		//
		let text = startSequence ? (startSequence + ps.rowIndex) + '' : (ps.rowIndex + 1) + ''

		let Icon = MK.getComponent('Icon')

		//click
		if (enableLink) {
			return (
				<div className='mk-datagrid-sequence-cell' onClick={onClick ? () => onClick(ps) : undefined}>
					{enableAddDelrow ? <Icon type="plus" className='mk-datagrid-editable-add-row' onClick={() => onAddrow ? onAddrow(ps) : undefined} /> : null}
					<a>
						{text}
					</a>
					{enableAddDelrow ? <Icon type="close" className='mk-datagrid-editable-remove-row' onClick={() => onDelrow ? onDelrow(ps) : undefined} /> : null}
				</div>
			)
		}

		return <div className='mk-datagrid-sequence-cell'>
			{enableAddDelrow ? <Icon type="plus" className='mk-datagrid-editable-add-row' onClick={() => onAddrow ? onAddrow(ps) : undefined} /> : null}
			<a style={{ color: "#444444", cursor: "default" }}>
				{text}
			</a>
			{enableAddDelrow ? <Icon type="close" className='mk-datagrid-editable-remove-row' onClick={() => onDelrow ? onDelrow(ps) : undefined} /> : null}
		</div>
	}



	return (
		<Column
			key="_sequence"
			width={42}
			fixed={true}
			cell={ps => getContent(ps)}
			header={
				<Cell></Cell>
			}
			footer={footer}
		/>
	)


}

